<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddWorkOrder') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditWorkOrder')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <Form ref="WorkOrder">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" 
                                ref="WorkOdetForm">
                                <template v-slot:tgslot-Mapslot="{ data }">
                                    <ManageMap class="row" mapType="MapBox" :config="config" mapContainer="WorkOrder"
                                        v-if="isShowMap" :Data="mapObj" @GetAddress="GetAddress" :key="componentKey">
                                    </ManageMap>
                                </template>
                            </dynamic-form>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import ManageMap from './../../components/Common/MapComponent.vue';
export default {
    components: { ManageMap },
    props: {
        WorkOrdrId: {
            type: Number,
            required: true
        },
        form: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            config: {
                isShowDrawButton: true,
                isShowEtaOrLocator: true,
                isShowSearchBox: true
            },
            isLoading: false,
            isShowMap: true,
            showSetting: false,
            clientId: 0,
            componentKey: 0,
            CompanyType: '',
            WorkOrderNumber: "",
            selectClientvalue: '',
            CountryDDl: '',
            StateDDl: '',
            selectvalue: '',
            Latitude: '',
            Longitude: '',
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            FormSchema: []
        }
    },
    created: async function () {
        var vueObj = this;
        if (vueObj.$parent.TicketId != null && vueObj.$parent.TicketNumber != null) {
            vueObj.tiketidNumber = vueObj.$parent.TicketId;
            let ticketNumberValue = {
                label: vueObj.$parent.TicketNumber,
                value: vueObj.$parent.TicketId

            }
            vueObj.FormSchema[0].Data[0].value = ticketNumberValue;
            vueObj.FormSchema[0].Data[0].disabled = true;
            vueObj.setClientData(null, vueObj.tiketidNumber);
        }
        vueObj.getForm();
        vueObj.IsExtraMapFeature();     
        setTimeout(function () {
            vueObj.GetCountryDDL();
        }, 2000);
    },
    methods: {
        getForm: function () {
            var vm = this;
            vm.isLoading = true;
            const params = `id=${vm.$route.params.id}&moduleName=Ticketing&subModuleCode=FSM_WORKORDER&langCode=en`;
            DataServices.GetDynamicForm(params)
                .then(function (response) {
                    vm.isLoading = false;
                    if (response != null && response.data != null) {
                        vm.FormSchema = vm.$refs.WorkOdetForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);                        
                        vm.FormSchema.forEach((group, gi) => {
                            group.Data.forEach(async (field, fi) => {
                                if (field.name == "work_type_id") {
                                    vm.GetDdlRecords_WorkType('FSM_WORKTYPE', field);
                                }
                                if (field.name == "PRODUCT_ID") {
                                    field.config.options = [];
                                }
                                if (field.name == "StartDateTime") {
                                    field.config.minDate = new Date();
                                }
                                if (field.name == "SA_Scheduled_StartTime") {
                                    field.config.minDate = new Date();
                                }
                                if (field.name == "Utility_Id") {
                                    field.config.options = [];
                                }
                                if (field.name == "client_id") {
                                    field.disabled = true;
                                }
                                if (field.name == "ticket_id") {
                                    field.config.options = [];
                                    field.config.onSearchChange = vm.onSearch;
                                }
                                field.config.onChange = vm.onChangeEventForFields;
                            })
                        });
                        vm.FormSchema.push(
                            {
                                layoutType: "triple",
                                group_name: 'Address',
                                Data: [
                                    {
                                        astype: "TextField",
                                        label: 'Address',
                                        name: "Address",
                                        value: "",
                                        placeholder: "",
                                        validationRules: { "required": true },
                                    },
                                    {
                                        astype: "SelectField",
                                        label: 'Country',
                                        name: "Country",
                                        value: "",
                                        disabled: true,
                                        config: {
                                            options: [],
                                        },
                                        placeholder: 'Select',
                                        validationRules: "",
                                    },
                                    {
                                        astype: "SelectField",
                                        label: 'State',
                                        name: "State",
                                        value: "",
                                        disabled: true,
                                        config: {
                                            options: []
                                        },
                                        placeholder: 'Select',
                                        validationRules: "",
                                    },
                                    {
                                        astype: "TextField",
                                        label: 'County',
                                        name: "County",
                                        value: "",
                                        disabled: true,
                                        placeholder: "",
                                        validationRules: "",
                                    },
                                    {
                                        astype: "TextField",
                                        label: 'City',
                                        name: "City",
                                        value: "",
                                        disabled: true,
                                        placeholder: "",
                                        validationRules: "",
                                    },
                                    {
                                        astype: "TextField",
                                        label: 'Street',
                                        name: "Street",
                                        value: "",
                                        disabled: true,
                                        placeholder: "",
                                        validationRules: "",
                                    },
                                    {
                                        astype: "TextField",
                                        label: 'ZipCode',
                                        name: "ZipCode",
                                        value: "",
                                        disabled: true,
                                        placeholder: "",
                                        validationRules: "",
                                    },
                                ]
                            });
                        vm.FormSchema.push(
                            {
                                layoutType: "single",
                                visibility: true,
                                Data: [{
                                    astype: "SlotField",
                                    name: "Mapslot",
                                    value: "",
                                    visibility: true,
                                    showLabel: true
                                }]
                            }
                        )
                    }
                })
        },
        GetAddress: function (data) {
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            var indexValue = data.length - 1;
            let filteredCountry = vueObj.CountryDDl.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });
            (vueObj.FormSchema)[3].Data[0].value = data[indexValue].CompleteAddress.FullAddress;
            var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
            (vueObj.FormSchema)[3].Data[1].value = filterCountry;
            vueObj.GetStateDDL(null, filterCountry);
            setTimeout(function () {
                let filteredState = vueObj.StateDDl.filter((item) => {
                    return item.name === data[indexValue].CompleteAddress.AdministrativeAreaLevel1.LongName;
                });
                var filterState = filteredState == '' ? '' : filteredState[0].value;
                (vueObj.FormSchema)[3].Data[2].value = filterState;
            }, 1000);
            (vueObj.FormSchema)[3].Data[3].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel2.LongName;
            (vueObj.FormSchema)[3].Data[4].value = data[indexValue].CompleteAddress.City.LongName;
            (vueObj.FormSchema)[3].Data[5].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel3.LongName;
            (vueObj.FormSchema)[3].Data[6].value = data[indexValue].CompleteAddress.PinCode.LongName;
            var dataLat1 = ''; var dataLat2 = ''; var dataLng1 = ''; var dataLng2 = '';
            data[indexValue].LatLng.forEach((item, index) => {
                if (index == 0) {
                    dataLat1 = item.Lat;
                    dataLat2 = item.Lng;
                }
                else {
                    dataLng1 = item.Lat;
                    dataLng2 = item.Lng;
                }
            });
            vueObj.Latitude = dataLng1 + ',' + dataLat2;
            vueObj.Longitude = dataLat1 + ',' + dataLng2;
        },
        Close: function () {
            this.$parent.AddWorkOrderModal = false;
        },
        IsExtraMapFeature: function () {
            var vueObj = this;
            DataServices.IsExtraMapFeature().then(
                function (response) {
                    vueObj.CompanyType = "";
                    if (response.data != null && response.data != "") {
                        vueObj.CompanyType = response.data;
                        if (vueObj.CompanyType == 'nonlocating' || vueObj.CompanyType == 'normal') {
                            vueObj.FormSchema[0].Data[7].visibility = false;
                            vueObj.FormSchema[0].Data[8].visibility = false;
                            vueObj.FormSchema[0].Data[7].validationRules = { "required": false };
                            vueObj.FormSchema[0].Data[8].validationRules = { "required": false };
                        }
                    } else {
                        vueObj.CompanyType = 'normal';
                        vueObj.FormSchema[0].Data[7].visibility = false;
                        vueObj.FormSchema[0].Data[8].visibility = false;
                        vueObj.FormSchema[0].Data[7].validationRules = { "required": false };
                        vueObj.FormSchema[0].Data[8].validationRules = { "required": false };
                    }
                });

        },
        GetStartData: function (event, id) {
            var vueObj = this;
            (vueObj.FormSchema)[2].Data[1].config.minDate = event;
        },
        onSearch: async function (e, selctedValue, search) {
            var vm = this;
            this.searchSelectData(search, this, selctedValue);
        },
        searchSelectData: async function (search, vm, selctedValue) {
            this.ShowSearchData(search, vm, selctedValue);
        },
        onChangeEventForFields: function (x, y, z) {
            let ths = this;
            var vm = this;
            if (y.name == "ticket_id") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "client_id") {
                            ths.setClientData(x, field, z.value);
                        }
                        if (field.name == "PRODUCT_ID") {
                            ths.GetProductRecords(x, field);
                        }                       
                    });
                });
            }
            if (y.name == "Utility_Id") {
                vm.GetCDCDdlData(y)
            }
            if (y.name == 'Address') {
                vm.fetchAddressByClientId(y, null)
            }               
            if(y.name == "StartDateTime"){                           
                vm.$refs.WorkOdetForm.updateProperty("EndDateTime", "config.minDate", x);
                vm.$refs.WorkOdetForm.UpdateKeyValue();                      
            } 
            if(y.name == "SA_Scheduled_StartTime"){                           
                vm.$refs.WorkOdetForm.updateProperty("SA_Scheduled_EndTime", "config.minDate", x);
                vm.$refs.WorkOdetForm.UpdateKeyValue();                      
            }       
        },
        setClientData: async function (x, y, id) {
            var vm = this;
            var TicketId = null;
            if (id != null) {
                TicketId = id;
            } else {
                TicketId = y.value;
            }
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'CLIENTFROMTICKETID'}&search=&refId=` + TicketId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    if (response.data.status == "Success") {
                        y.value = response.data.result.DATA[0].value;
                        y.disabled = true;
                        vm.GetClientaddress(null, response.data.result.DATA[0].value);
                        vm.GetUtilityDDL(null, response.data.result.DATA[0].value)
                    }
                    else {
                        vm.selectClientvalue = '';
                    }
                }
            });
        },
        GetDdlRecords_WorkType: async function (x, y, id) {
            var vm = this;
            y.config.options = [];
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_WORKTYPE'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        if (response.data.result.DATA.length > 0) {
                            y.config.options = response.data.result.DATA;
                            vm.$refs.WorkOdetForm.UpdateKeyValue();
                        }
                    }
                }
            });
        },
        GetUtilityDDL: async function (x, y, id) {
            var clientId = 0;
            if (id != null) {
                clientId = id;
            } else {
                clientId = y;
            }
            var vm = this;
            var appendingUrl = `refId=&&moduleName=${'Ticketing'}&type=${'UTILITY_LIST_BY_CLIENT_ID'}&search=&id=${clientId}`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        if (response.data.result.DATA.length > 0) {
                            vm.FormSchema.forEach((group, gi) => {
                                group.Data.forEach((field, fi) => {
                                    if (field.name == "Utility_Id") {
                                        field.config.options = response.data.result.DATA;
                                    }
                                    vm.$refs.WorkOdetForm.UpdateKeyValue();
                                });
                            });
                        }
                    }
                }
            });
        },
        ShowSearchData: async function (search, vm, selctedValue) {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_Tickets_List'}&search=${search}&refId=`;
            if (typeof (selctedValue) == 'undefined') {
                selctedValue = 0;
            }
            selctedValue.config.options = [];
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            selctedValue.config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetProductRecords: async function (type,field) {
            var vm = this;
            type='PRODUCT';
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${type}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        if(response.data.result.DATA.length>0){
                            field.config.options=response.data.result.DATA;
                        }
                        vm.$refs.WorkOdetForm.UpdateKeyValue();                        
                    }
                }
            });
        },
        GetClientaddress: async function (event, id) {
            var vm = this;
            var clientId = 0;
            if (event == null) {
                clientId = id;
            } else {
                clientId = event.target.value.toString();
                vm.clientId = clientId;
            }
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'CLIENT_ADDRESS_LIST_WO'}&search=&refId=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (field.name == "Address") {
                                    field.config.options = response.data.result.DATA;
                                }
                                vm.$refs.WorkOdetForm.UpdateKeyValue();
                            });
                        });
                    }
                }
            });
        },
        fetchAddressByClientId: async function (event, id) {
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            var addressId = 0;
            if (event == null) {
                addressId = id;
            } else {
                addressId = event.value;
            }
            var appendingUrl = `id=${vueObj.clientId}&&moduleName=${'Ticketing'}&&addressId=` + addressId;
            await DataServices.GetTicketClientAddress(appendingUrl).then(function (response) {
                if (response.data) {
                    var countryId = response.data.result.data[0].COUNTRY_ID;
                    vueObj.GetStateDDL(null, countryId);
                    (vueObj.FormSchema)[3].Data[0].value = response.data.result.data[0].USER_ADDRESS_1;
                    (vueObj.FormSchema)[3].Data[1].value = response.data.result.data[0].COUNTRY_ID;
                    (vueObj.FormSchema)[3].Data[2].value = response.data.result.data[0].CT[0].ST[0].STATE_ID;
                    (vueObj.FormSchema)[3].Data[3].value = response.data.result.data[0].USER_STATE;
                    (vueObj.FormSchema)[3].Data[4].value = response.data.result.data[0].USER_CITY;
                    (vueObj.FormSchema)[3].Data[5].value = response.data.result.data[0].USER_ADDRESS_2;
                    (vueObj.FormSchema)[3].Data[6].value = response.data.result.data[0].USER_ZIPCODE;
                    vueObj.Latitude = response.data.result.data[0].Latitude + ',' + response.data.result.data[0].Longitude;
                    vueObj.Longitude = response.data.result.data[0].Latitude + ',' + response.data.result.data[0].Longitude;
                    vueObj.$refs.WorkOdetForm.UpdateKeyValue();
                }
            });
        },
        GetCDCDdlData: async function (event) {
            var vm = this;
            var clientId = event.value;
            var appendingUrl = `refId=&&moduleName=${'Ticketing'}&type=${'FSM_CDCCODE_FOR_TICKET'}&search=&id=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (field.name == "CDC_Id") {
                                    field.config.options = response.data.result.DATA;
                                }
                                vm.$refs.WorkOdetForm.UpdateKeyValue();
                            });
                        });

                    }
                }
            });
        },
        GetCountryDDL: async function () {          
            var vm = this;
            vm.CountryDDl = '';
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'COUNTRIES'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.CountryDDl = response.data.result.DATA;
                    if (json != null) {
                        vm.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (field.name == "Country") {
                                    field.config.options = response.data.result.DATA;
                                }
                                vm.$refs.WorkOdetForm.UpdateKeyValue();
                            });
                        });
                    }
                }
            });
        },
        GetStateDDL: async function (event, id) {
            var vm = this;
            var CountryId = 0;
            if (event == null) {
                CountryId = id;
            } else {
                CountryId = event.target.value.toString();
            }
            vm.FormSchema[3].Data[2].config.options = [];
            vm.StateDDl = '';
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'STATE'}&search=&refId=` + CountryId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.StateDDl = response.data.result.DATA;
                    if (json != null) {
                        vm.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (field.name == "State") {
                                    field.config.options = response.data.result.DATA;
                                }
                                vm.$refs.WorkOdetForm.UpdateKeyValue();
                            });
                        });
                    }
                }
            });
        },
        saveForm: function () {
            var vm = this;          
            vm.buttons[0].class+=" disabled";  
            vm.$refs.WorkOrder.validate().then(result => {
                if (result) {
                    vm.isLoading = true;
                    var CustomFieldArray = vm.$refs.WorkOdetForm.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema); 
                    CustomFieldArray.forEach((x) => {
                        if (x.field_name == "LOCATION_NW") {
                            x.field_value = vm.Latitude;
                        }
                        if (x.field_name == "LOCATION_SE") {
                            x.field_value = vm.Longitude;
                        }
                        if (x.field_name == "id") {
                            x.field_value = vm.WorkOrdrId == null ? 0 : vm.WorkOrdrId;
                        }
                        if (x.field_name == "workorder_number") {
                            x.field_value = vm.WorkOrderNumber;
                        }
                        if (x.field_name == "status_id") {
                            x.field_value = 1001;
                        }
                        if (x.field_name == "ticket_id") {                            
                            x.field_value = x.field_value.value;
                        }
                        
                    });                    
                    var obj = {
                        data: CustomFieldArray,
                        moduleCode: "Ticketing",
                        subModuleCode: "FSM_WORKORDER"
                    }                    
                    DataServices.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                        if (response != null && response.data != null && response.data[0].Status == "Success") {                            
                            if (response.data[0].Value.MSG == 'EXIST') {
                                vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("WorkOrder")]), "failure", true, vm.$t("Alert"));
                            }
                            else {
                                if (vm.WorkOrdrId > 0) {
                                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('WorkOrder')]), "success", true, vm.$t("Alert"));
                                    vm.Close();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('WorkOrder')]), "success", true, vm.$t("Alert"));
                                    vm.Close();
                                }
                            }
                            if (vm.form == 'WorkOrder') {
                                vm.$parent.fetchRecords()
                                vm.Close();
                            } else {
                                vm.$parent.IsWorkOrderShow = true;
                                vm.$parent.isShowWorkList = false;
                                vm.$parent.isShowWorkOrder = false;
                                vm.$parent.LoadTab('workorder')
                                vm.Close();
                            }

                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("WorkOrder")]), "failure", true, vm.$t("Alert"));
                            vm.Close();
                        }
                    })
                    vm.isLoading = false;

                }
            })
        }
    }
}
</script>